import utils from './utils';

/**
 * Полноэкранная логика прокрутки
 */
class PureFullPage {
  constructor(options) {
    // распределение по умолчанию
    const defaultOptions = {
      isShowNav: true,
      delay: 1000,
      // definePages: () => { },
    };
    utils.polyfill();
    // объединить пользовательскую конфигурацию
    // this.options = Object.assign(defaultOptions, options);
    this.options = options || defaultOptions;

    // Привязываем пользовательскую функцию к экземпляру this
    // this.options.definePages = this.options.definePages.bind(this);
    // Получить контейнер перелистывания страниц
    this.container = document.querySelector('.main-content');
    // Получаем общее количество страниц, использованных при создании правой точки навигации
    this.pages = document.querySelectorAll('.section');
    this.pagesNum = this.pages.length;
    // Инициализируем навигацию с правой стороны для дальнейшего использования
    this.navDots = [];
    // Получить текущую высоту просмотра
    this.viewHeight = document.documentElement.clientHeight;
    // Текущая позиция, отрицательное значение представляет собой
    // смещение вниз от верха окна просмотра
    this.currentPosition = 0;
    // время задержки функции перехвата / интервала, миллисекунды
    this.DELAY = this.options.delay;
    // Чтобы определить направление скольжения, нужно определить только ординату
    this.startY = undefined;

    /**
     * Инициализация
     */
    this.init();
  }
  // Повторное получение позиции при изменении размера окна

  getNewPosition() {
    this.viewHeight = document.documentElement.clientHeight;
    this.container.style.height = `${this.viewHeight}px`;
    let activeNavIndex;
    this.navDots.forEach((e, i) => {
      if (e.classList.contains('active')) {
        activeNavIndex = i;
      }
    });
    this.currentPosition = -(activeNavIndex * this.viewHeight);
    this.turnPage(this.currentPosition);
  }

  handleWindowResize(event) {
    // Устанавливаем функцию защиты от сотрясения
    utils.debounce(this.getNewPosition, this, event, this.DELAY);
  }
  // переход на страницу

  turnPage(height) {
    this.container.style.top = `${height}px`;
  }
  // Изменяем стиль при прокрутке страницы

  changeNavStyle(height) {
    if (this.options.isShowNav) {
      this.navDots.forEach(el => {
        utils.deleteClassName(el, 'active');
      });

      const i = -(height / this.viewHeight);
      this.navDots[i].classList.add('active');
    }
  }
  // Создание навигации по правой стороне

  createNav() {
    const nav = document.createElement('div');
    nav.className = 'nav';
    this.container.appendChild(nav);

    // Есть несколько страниц, показывающих несколько точек
    for (let i = 0; i < this.pagesNum; i += 1) {
      nav.innerHTML += '<p class="nav-dot"><span></span></p>';
    }

    const navDots = document.querySelectorAll('.nav-dot');
    this.navDots = Array.prototype.slice.call(navDots);

    // Добавляем начальный стиль
    this.navDots[0].classList.add('active');

    // Добавить событие click по точке навигации
    this.navDots.forEach((el, i) => {
      el.addEventListener('click', () => {
        // переход на страницу
        this.currentPosition = -(i * this.viewHeight);
        // Обрабатываем пользовательские функции
        // this.options.definePages();
        this.turnPage(this.currentPosition);

        // изменить стиль
        this.navDots.forEach((el) => {
          utils.deleteClassName(el, 'active');
        });
        el.classList.add('active');
      });
    });
  }

  goUp() {
    // Страница прокручивается вверх только тогда, когда есть страницы вверху страницы
    if (-this.container.offsetTop >= this.viewHeight) {
      // Повторно укажите currentPosition текущей страницы из верхней части представления,
      // чтобы обеспечить полноэкранную прокрутку.CurrentPosition - отрицательное значение,
      // и чем больше значение, тем меньше часть за верхним
      this.currentPosition += this.viewHeight;

      this.turnPage(this.currentPosition);
      this.changeNavStyle(this.currentPosition);
      // Обрабатываем пользовательские функции
      // this.options.definePages();
    }
  }

  goDown() {
    // Страница прокручивается вниз только тогда, когда есть страницы внизу страницы
    if (-this.container.offsetTop <= this.viewHeight * (this.pagesNum - 2)) {
      // Повторно укажите currentPosition текущей страницы из верхней части представления,
      // чтобы обеспечить полноэкранную прокрутку.CurrentPosition - отрицательное значение,
      // и чем меньше значение, тем больше часть за верхним
      this.currentPosition -= this.viewHeight;

      this.turnPage(this.currentPosition);
      this.changeNavStyle(this.currentPosition);

      // Обработка пользовательских функций
      // this.options.definePages();
    }
  }
  // Логика прокрутки мыши (логика клавиш для полноэкранной прокрутки)

  scrollMouse(event) {
    const delta = utils.getWheelDelta(event);
    // дельта <0, прокрутка мыши вперед, прокрутка страницы вниз
    if (delta < 0) {
      this.goDown();
    } else {
      this.goUp();
    }
  }
  // событие сенсорного экрана

  touchEnd(event) {
    const endY = event.changedTouches[0].pageY;
    if (endY - this.startY < 0) {
      // Проведите пальцем вверх, прокрутите соответствующую страницу вниз
      this.goDown();
    } else {
      // Проведите пальцем вниз, прокрутите соответствующую страницу вверх
      this.goUp();
    }
  }
  // Функция инициализации

  init() {
    this.container.style.height = `${this.viewHeight}px`;
    // Создание точечной навигации
    if (this.options.isShowNav) {
      this.createNav();
    }
    // Устанавливаем функцию перехвата
    const handleMouseWheel = utils.throttle(this.scrollMouse, this, this.DELAY);

    // Мониторинг колесика мыши, события прокрутки мыши Firefox отличаются от других
    if (navigator.userAgent.toLowerCase().indexOf('firefox') === -1) {
      document.addEventListener('mousewheel', handleMouseWheel);
    } else {
      document.addEventListener('DOMMouseScroll', handleMouseWheel);
    }

    // сенсорный экран мобильного телефона
    document.addEventListener('touchstart', event => {
      this.startY = event.touches[0].pageY;
    });
    const handleTouchEnd = utils.throttle(this.touchEnd, this, 500);
    document.addEventListener('touchend', handleTouchEnd);
    document.addEventListener('touchmove', event => {
      event.preventDefault();
    });

    // Сбрасываем позицию при изменении размера окна
    window.addEventListener('resize', this.handleWindowResize.bind(this));
  }
}

const fullpage = new PureFullPage({
  delay: 1000,
});
